    font-size: 2.8rem;
  }
  .collection-item__footer-item--right {
    font-size: 3rem;
    font-style: normal;
    font-weight: 400;
    line-height: 140%; /* 28px */
  }
  .collection-item__footer-item--right strong {
    font-size: 3.6rem;
    margin: 0 0.5rem;
    align-self: center;
  }
  .collection-item__footer-item--right span {
    line-height: 1.1;
    margin-bottom: 0;
  }
  .collection-item__footer-item--right sup {
    line-height: 1.1;
  }
}
.main-links {
  margin-top: 5.2rem;
  margin-bottom: 0;
}
.main-links__list {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.main-links__item {
  width: 100%;
  text-align: center;
  color: #ffffff;
  font-family: "DrukCyr";
  font-size: 10rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  transition: background 0.3s linear, color 0.3s linear;
  border-top: 0.1rem solid rgba(201, 243, 43, 0.5);
}
.main-links__item:hover {
  transition: background 0.3s linear, color 0.3s linear;
  background-color: #fad85d;
  color: #282828;
}
.main-links__item a {
  display: block;
  width: 100%;
  padding: 0.6rem 0;
}
.main-links__item--big {
  font-size: 19.9646rem;
  line-height: 23.387rem; /* 117.143% */
}
.main-links__item--big a {
  padding: 1.8rem 0 3.4rem;
}

@media (max-width: 48em) {
  .main-links {
    margin-top: 0;
    margin-bottom: 7.6rem;
  }
  .main-links__list {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .main-links__item {
    line-height: 110%;
    border-top: 0.2rem solid rgba(201, 243, 43, 0.5);
  }
  .main-links__item a {
    padding: 2.4rem 0;
  }
  .main-links__item--big {
    font-size: 11rem;
    line-height: 110%; /* 117.143% */
  }
  .main-links__item--big a {
    padding: 2.4rem 0;
  }
}
.main-rates__inner {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 9rem;
}
.main-rates__title {
  color: #ffffff;
  font-family: "DrukCyr";
  font-size: 10rem;
  font-style: normal;
  font-weight: 500;
  line-height: 110%; /* 110px */
  text-transform: uppercase;
}
.main-rates__wrapper {
  display: flex;
  flex-direction: column;
  gap: 4rem;
  width: 100%;
}
.main-rates__item {
  height: 18rem;
  display: flex;
  align-items: center;
  border-radius: 3rem;
}

.rates-item {
  position: relative;
}
.rates-item::before, .rates-item::after {
  content: "";
  width: 5.1rem;
  height: 5.1rem;
  border-radius: 50%;
  background: #1f1f22;
  position: absolute;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);
}
.rates-item::after {
  right: -2.5rem;
}
.rates-item::before {
  left: -2.5rem;
}
.rates-item--bg--pink {
  background: #ff69c2;
}
.rates-item--bg--blue {
  background: #cad2f4;
}
.rates-item--bg--green {
  background: #c9fb40;
}
.rates-item__left {
  padding: 3.6rem 3.6rem 3.6rem 8.6rem;
  height: 100%;
  width: 57.8rem;
  display: flex;
  align-items: center;
  border-right: 0.3rem dashed #282828;
}
.rates-item__title {
  color: #1f1f22;
  font-size: 2.8rem;
  font-weight: 500;
  line-height: normal;
}
.rates-item__right {
  display: flex;
  align-items: center;
  gap: 20.2rem;
  height: 100%;
  padding-left: 9.3rem;
}
.rates-item__price-top {
  color: #1f1f22;
  font-size: 2rem;
  font-style: normal;
  font-weight: 400;
  line-height: 140%; /* 28px */
}
.rates-item__price-top strong {
  color: #1f1f22;
  font-size: 2.8rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
  margin: 0 0.5rem;
  align-self: center;
}
.rates-item__price-top span {
  align-self: flex-end;
  margin-bottom: 0.2rem;
}
.rates-item__price-top sup {
  align-self: flex-start;
}
.rates-item__price-bottom {
  color: #282828;
  font-size: 2rem;
  font-style: normal;
  font-weight: 600;
  line-height: 140%; /* 28px */
}
.rates-item__options {
  width: 16rem;
  color: #282828;
  font-size: 2rem;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}
.rates-item__icon {
  position: absolute;
}

.rates-icon--sunny {
  top: -5rem;
  left: 14.3rem;
  width: 9.7rem;
  height: 8rem;
  transform: rotate(-7.376deg);
}
.rates-icon--shapes {
  right: 35.6rem;
  top: -4.3rem;
  width: 9.2rem;
  height: 9rem;
  transform: rotate(-15deg);
}
.rates-icon--megafon {
  width: 8.7rem;
  height: 7rem;
  transform: rotate(-12.401deg);
  right: 68.6rem;
  bottom: -3.5rem;
}

@media (max-width: 48em) {
  .main-rates__inner {
    gap: 10rem;
  }
  .main-rates__item {
    height: 56rem;
    border-radius: 4rem;
    flex-direction: column;
  }
  .rates-item::before, .rates-item::after {
    width: 9rem;
    height: 9rem;
  }
  .rates-item::after {
    right: -4.5rem;
  }
  .rates-item::before {
    left: -4.5rem;
  }
  .rates-item__left {
    padding: 4.8rem;
    height: 13.4rem;
    width: 100%;
    justify-content: center;
    border-right: 0;
    border-bottom: 0.3rem dashed #282828;
  }
  .rates-item__title {
    font-size: 3rem;
  }
  .rates-item__right {
    flex-direction: column;
    width: 100%;
    gap: 4rem;
    height: auto;
    padding-left: 0;
    padding-top: 2.4rem;
  }
  .rates-item__price-top {
    font-size: 4rem;
  }
  .rates-item__price-top strong {
    font-size: 5.6rem;
    margin: 0 1rem;
  }
  .rates-item__price-top span {
    align-self: flex-end;
    margin-bottom: 0;
  }
  .rates-item__price-top sup {
    align-self: flex-start;
  }
  .rates-item__price-bottom {
    color: #282828;
    font-size: 2.8rem;
    font-style: normal;
    font-weight: 600;
    line-height: 140%; /* 28px */
  }
  .rates-item__options {
    width: 80%;
    text-align: center;
    font-size: 2.8rem;
    display: flex;
    justify-content: center;
    margin-bottom: 2.8rem;
  }
  .rates-item__icon {
    display: none;
  }
